<template>
	<view class="content">
		<u-navbar :is-back="false" back-icon-color="#fff" title-color="#fff" title="志愿活动" :background="background"
			title-bold>
			<view slot="right" style="margin-right: 30rpx;" @click="torelease">
				<u-icon name="edit-pen-fill" style="margin-right: 8rpx;"></u-icon>发起活动
			</view>
		</u-navbar>
		<u-row class="tabsList">
			<u-col span="12" style="padding: 23rpx 25rpx 24rpx 25rpx;">
				<u-search shape="round" v-model="keyword" @search="Tosearch" @custom="Tosearch"></u-search>
			</u-col>
			<u-col span="12">
				<u-tabs active-color="#fff" inactive-color="#B1D1FF" :list="list" :current="current"
					@change="change"></u-tabs>
			</u-col>
		</u-row>
		<view style="margin: -71rpx 30rpx 0 30rpx;">
			<scroll-view scroll-y style="height: 920rpx;" v-show="current==0">
				<view class="list02" v-for="(item,index) in zxlist" :key="index" @click="details(item)">
					<u-row>
						<u-col span="9">
							<view class="blueline"></view>
							<span>{{item.universalAntiAboutMain26}}</span>
							<b class="tiptxt">报名中</b>
						</u-col>
						<u-col span="12">
							<p>{{item.activityContent}}</p>
						</u-col>
						<u-col span="9" style="margin-top: 19rpx;">
							<image src="/static/img/bz01.png" mode="widthFix" class="bz01"></image>{{item.address}}
						</u-col>
						<u-col span="3" style="text-align: right;margin-top: 19rpx;">
							<image src="/static/img/bz02.png" mode="widthFix" class="bz02"></image>
							<b>{{item.nowNumberPeople}}</b>/{{item.numberPeople}}
						</u-col>
					</u-row>
				</view>
			</scroll-view>
			<scroll-view scroll-y style="height: 920rpx;" v-show="current==1">
				<view class="list02" v-for="(item,index) in zxlist" :key="index" @click="details(item)">
					<u-row>
						<u-col span="9">
							<view class="blueline"></view>
							<span>{{item.universalAntiAboutMain26}}</span>
							<b class="tiptxt">已结束</b>
						</u-col>
						<u-col span="12">
							<p>{{item.activityContent}}</p>
						</u-col>
						<u-col span="9" style="margin-top: 19rpx;">
							<image src="/static/img/bz01.png" mode="widthFix" class="bz01"></image>{{item.address}}
						</u-col>
						<u-col span="3" style="text-align: right;margin-top: 19rpx;">
							<image src="/static/img/bz02.png" mode="widthFix" class="bz02"></image>
							<b>{{item.nowNumberPeople}}</b>/{{item.numberPeople}}
						</u-col>
					</u-row>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import {
		trendsObj,
		getDicts
	} from '../../api/index.js'
	export default {
		data() {
			return {
				params: {
					pageNum: 1,
					pageSize: 10,
					queryJson: []
				},
				background: {
					backgroundColor: '#1F61BE',
					color: "#fff"
				},
				search: 1,
				keyword: '',
				show: 1,
				form: {},
				current: 0,
				swiperCurrent: 0,
				list: [{
					name: '报名中'
				}, {
					name: '已结束'
				}],
				list01: [{
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, {
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, {
					name: '反恐防恐小常识：如何识别恐怖袭击嫌疑人？',
					time: '3天前',
					pic: '/static/img/banner.png'
				}, ],
				zxlist: [],
				statusArr: [],
				statusArr1: []
			}
		},
		onLoad() {
			/*getDicts('universal_activity_status').then(res => {
				if (res.code == 200) {
					this.statusArr1 = res.data;
				}
			})*/
			getDicts('work_enter_status').then(res => {
				if (res.code == 200) {
					this.statusArr = res.data;
				}
			})
			this.params.queryJson[0] = {
				data1: 'aboutEnterStatus',
				data2: '0',
				data3: "2",
				objectId: "294",
				subType: null,
				flag: false,
				fileType: "String"
			};
			this.params.queryJson = JSON.stringify(this.params.queryJson);
			this.listall();
		},
		methods: {
			details(item) {
				if (uni.getStorageSync('isLeader')) {
					uni.navigateTo({
						url: '../activity/detailstabs?id=' + item.id
					})
				} else {
					uni.navigateTo({
						url: '../activity/details?id=' + item.id 
					})
				}
			},
			torelease() {
				uni.navigateTo({
					url: 'release'
				})
			},
			fystatus(name) {
				let result = null;
				this.statusArr.map((item) => {
					if (item.dictValue == name) {
						result = item.dictLabel;
					}
				})
				return result;
			},
			listall() {
				trendsObj(this.params).then(res => {
					if (res.code == 200) {
						this.zxlist = res.rows;
					}
				})
			},
			Tosearch() {
				if (this.keyword) {
					this.params.queryJson = [];
					this.params.queryJson[0] = {
						data1: 'aboutEnterStatus',
						data2: '0',
						data3: this.current + 1,
						objectId: "294",
						subType: null,
						flag: false,
						fileType: "String"
					};
					this.params.queryJson[1] = {
						data1: 'universalAntiAboutMain26',
						data2: '2',
						data3: this.keyword,
						objectId: "294",
						subType: null,
						flag: false,
						fileType: "String"
					};
					this.params.queryJson = JSON.stringify(this.params.queryJson);
					this.listall();
				} else {

				}
			},
			change(index) {
				this.current = index;
				this.params.queryJson = [];
				this.params.queryJson[0] = {
					data1: 'aboutEnterStatus',
					data2: '0',
					data3: this.current + 2,
					objectId: "294",
					subType: null,
					flag: false,
					fileType: "String"
				};
				this.params.queryJson = JSON.stringify(this.params.queryJson);
				this.listall();
			},
			transition(e) {
				let dx = e.detail.dx;
				this.$refs.uTabs.setDx(dx);
			},
			animationfinish(e) {
				let current = e.detail.current;
				this.$refs.uTabs.setFinishCurrent(current);
				this.swiperCurrent = current;
				this.current = current;
			},
		}
	}
</script>

<style scoped>
	.content {
		width: 100vw;
		height: 100vh;
		background-color: #F5F6FA;
	}
	.content /deep/ .u-action{
		color: #fff;
	}

	.btn {
		background-color: #296DCB;
		color: #fff;
		font-size: 24rpx;
		margin-top: 40rpx;
	}

	.tabsList {
		background-color: #1F61BE;
		border-radius: 0 0 10rpx 10rpx;
		padding-bottom: 71rpx;
	}

	.tabsList /deep/ .u-tabs {
		background: transparent !important;
	}

	.topBox {
		position: relative;
		font-size: 32rpx;
		font-weight: bold;
		color: #333333;
	}

	.topBox .ss {
		position: absolute;
		right: -10rpx;
		top: 8rpx;
		width: 30rpx;
		height: 30rpx;
	}

	.grid-text {
		font-size: 24rpx;
		font-weight: bold;
		color: #333333;
		margin-top: 16rpx;
	}

	.list01 {
		width: 100%;
		height: 172rpx;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 20rpx 20rpx 20rpx 30rpx;
		position: relative;
	}

	.blueline {
		position: absolute;
		left: 0;
		top: 30rpx;
		width: 6rpx;
		height: 28rpx;
		background: #0062E9;
	}

	.list01 span,
	.list02 span {
		font-size: 28rpx;
		font-weight: bold;
		color: #333333;
	}

	.list01 p,
	.list02 p {
		margin-top: 20rpx;
		font-size: 24rpx;
		color: #999999;
		width: 90%;
		/*宽度记得设置，这样超出宽度的内容才会被省略*/
		overflow: hidden;
		/*文本超出隐藏*/
		white-space: nowrap;
		text-overflow: ellipsis;
		/*文本超出省略*/
	}

	.list02 b {
		font-size: 24rpx;
		color: #296DCB;
		font-weight: normal;
	}

	.tiptxt {
		position: absolute;
		right: 32rpx;
		top: 20rpx;
	}

	.listimg {
		width: 208rpx;
		height: 126rpx;
	}

	.list02 {
		width: 100%;
		height: 231rpx;
		background: #FFFFFF;
		box-shadow: 0rpx 2rpx 8rpx 1rpx rgba(0, 0, 0, 0.06);
		border-radius: 10rpx 10rpx 10rpx 10rpx;
		margin-top: 22rpx;
		padding: 17rpx 30rpx;
		position: relative;
		font-size: 24rpx;
		font-weight: 400;
		color: #333333;
	}

	.bz01 {
		width: 52rpx;
		vertical-align: middle;
		display: inline-block;
		margin-left: -10rpx;
	}

	.bz02 {
		width: 28rpx;
		margin-right: 6rpx;
		vertical-align: middle;
		display: inline-block;
	}
</style>